body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  background-color: #f7f7f7; }

.musement-app {
  display: flex;
  flex-direction: column;
  height: 100vh; }
  .musement-app .product-global-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }

@media (max-width: 900px) {
  .product-global-wrapper {
    display: flex;
    flex-direction: column;
    align-content: center; } }

.header-wrapper {
  display: flex;
  justify-content: space-between;
  background-color: white;
  border-radius: 10px;
  height: 4rem;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  margin-top: 15px; }
  .header-wrapper .header-left-side {
    display: flex; }
  .header-wrapper .header-logo {
    height: 50px;
    width: 50px;
    padding: 12px; }
  .header-wrapper .header-brand {
    padding: 20px; }
  .header-wrapper .header-features {
    display: flex;
    padding: 15px;
    margin-top: 15px; }

.feature-wrapper {
  display: flex;
  position: relative; }
  .feature-wrapper .feature-icon {
    position: relative;
    height: 30px;
    width: 30px;
    margin: 0 8px; }
    .feature-wrapper .feature-icon .feature-number {
      position: absolute;
      right: 0;
      top: 0;
      background-color: teal;
      font-size: 9px;
      color: white;
      height: 15px;
      width: 15px;
      border-radius: 7.5px;
      margin-top: -10px;
      margin-right: -4px;
      text-align: center; }
    .feature-wrapper .feature-icon .feature-img {
      position: absolute;
      left: 0;
      bottom: 10; }

.footer-wrapper {
  display: flex;
  justify-content: flex-start;
  background-color: white;
  border-radius: 10px;
  height: 40px;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  margin-bottom: 15px;
  margin-top: 20px; }
  .footer-wrapper .footer-title {
    margin-left: 10px; }

@font-face {
  font-family: 'Lato-Bold';
  src: url(../../static/media/Lato-Bold.44dfe8cc.ttf); }

@font-face {
  font-family: 'Lato-Regular';
  src: url(../../static/media/Lato-Regular.7f690e50.ttf); }

.product-wrapper {
  background-color: white;
  border-radius: 10px;
  height: 500px;
  width: 25%;
  margin: 25px 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 10px 10px 9px 0px #b3b3b3;
  position: relative; }
  .product-wrapper .product-star-wrapper {
    position: absolute;
    top: 10px;
    right: 10px; }
  .product-wrapper .product-star {
    z-index: 1;
    width: 30px;
    height: 30px;
    border-style: solid;
    border-radius: 15px;
    border-color: lightgray;
    background-color: white;
    border-width: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer; }
    .product-wrapper .product-star:hover {
      background-color: #f0f0f0; }
  .product-wrapper .yellow {
    background-color: #fcfc91; }
  .product-wrapper .product-img-wrapper {
    width: 80%;
    height: 145px;
    overflow: hidden;
    margin-top: 25px;
    margin-bottom: 15px; }
    .product-wrapper .product-img-wrapper .product-img {
      width: 300px;
      height: 145px;
      object-fit: cover; }
  .product-wrapper .product-title {
    font-family: "Lato-Bold";
    margin: 0 10px;
    font-size: 1.1rem;
    padding: 8px;
    text-align: center; }
  .product-wrapper .product-description {
    font-family: "Lato-Regular";
    margin: 0px 30px;
    font-size: 0.7rem;
    padding: 8px;
    text-align: center;
    margin-bottom: auto; }
  .product-wrapper .product-price {
    font-family: "Lato-Bold";
    font-size: 1.1rem;
    padding: 8px; }
  .product-wrapper .product-button {
    display: flex;
    height: 40px;
    padding: 15px;
    border-radius: 20px;
    border-style: none;
    width: 75%;
    margin-bottom: 20px;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
  .product-wrapper .add {
    background-color: #f1f1f1;
    box-shadow: 10px 10px 8px -6px #b3acb3; }
  .product-wrapper .cart {
    background-color: #aadde0;
    box-shadow: 10px 10px 8px -6px #b3acb3; }

@media (max-width: 900px) {
  .product-wrapper {
    width: 35%; } }

@media (max-width: 750px) {
  .product-wrapper {
    width: 50%; } }

@media (max-width: 500px) {
  .product-wrapper {
    width: 60%; } }

.pagination-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 30px 0;
  margin-bottom: auto; }
  .pagination-wrapper .pagination-page {
    margin: 0 15px;
    cursor: pointer; }
  .pagination-wrapper .current-page {
    font-weight: bold; }
  .pagination-wrapper .pagination-arrow {
    margin: 0 10px;
    cursor: pointer; }

